@import "variables.scss";

.about-hotel {
  max-width: 1920px;
  margin-top: 90px;

  &-container {
    max-width: 1170px;
    margin: 0 auto;

    @media (max-width: 1170px) {
      padding-left: 10px;
      padding-right: 10px;
    }
  }

  &__main {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
  }

  &__slider {
    @media (max-width: 1000px) {
      display: none;
    }
  }

  &__text {
    margin-top: 15px;
    padding-left: 60px;

    @media (max-width: 1000px) {
      padding-left: 0px;
    }
  }

  &__text-main {
    font-size: 14px;
  }

  &__text-discount {
    font-size: 14px;
    margin-top: 30px;
    color: #f93b6c;
  }

  &__slider {
    max-width: 600px;
    max-height: 400px;
    height: 100%;
    width: 100%;
  }
}

.about-hotel__item {
  height: 400px;
  width: 600px;
}

.carousel-indicators li {
  border-radius: 50%;
  width: 10px;
  height: 10px;
  background-color: #fff;
}

.carousel-indicators .active {
  background-color: #fefefe;
}